.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.card_name {
  font-family: "Cabinet Grotesk", Arial, sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.2;
  text-align: center;
  position: relative;
  top: 40px
}

.card_image {
  width: 328px;
  height: 328px;
  border: 1px solid #c0c0c0;
  border-radius: 20px;
}
