.page {
  max-width: 1440px;
  max-height: 80px;
  width: 100vw;
  height: 80px;
  margin: 0 auto;
}

.weather-type-selector {
  border: none;
  margin-top: 24px;
  margin-bottom: 32px;
}

.radio-button {
  margin: 8px 0;
}
.radio-button_label {
  font-weight: 500;
}

.radio-button label {
  font-weight: 500;
}

.radio-container radio-button_label + input[type="radio"]:checked {
  font-weight: 700;
}

.modal__form_item {
  height: 52px;
  margin-top: 24px;
}